import Decode from "@/utils/decode.js"
import uCharts from '@/js_sdk/u-charts/u-charts/u-charts.js';
import { getStep } from "@/api/mine.js"
import { getOpenID } from "@/api/mine.js"
let self;
let canvaColumnWeek = {};
let canvaColumn = {};
export default {
	data() {
		return {
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			serverData: '',

			// chartData: {
			// 	categories: [],
			// 	series: [{
			// 		name: "运动步数",
			// 		data: [10,{ "value": 40, "color": "#facc14" }]
			// 	}]
			// }
			chartData: {
				categories: [],
				series: [{
					name: "最近一月运动步数",
					data: []
				}],
				type: "column"
			},
			chartDataWeek: {
				categories: [],
				series: [{
					name: "最近一周运动步数",
					data: []
				}],
				type: "column"
			}
		};
	},
	methods: {

		showColumn(canvasId, chartData, status) {
			
			if (!status) {
				
				console.log(canvasId, chartData)
				canvaColumn = new uCharts({
					$this: self,
					canvasId: canvasId,
					type: chartData.type,
					colors: ["#8dc63f"],
					legend: { show: true },
					fontSize: 11,
					background: '#FFFFFF',
					pixelRatio: self.pixelRatio,
					animation: true,
					categories: chartData.categories,
					series: chartData.series,
					xAxis: {
						disableGrid: true,
					},
					yAxis: {
						//disabled:true
					},
					dataLabel: true,
					width: self.cWidth * self.pixelRatio,
					height: self.cHeight * self.pixelRatio,
					extra: {
						column: {
							type: 'group',
							width: self.cWidth * self.pixelRatio * 0.45 / chartData.categories.length
						}
					}
				});
			} else {
				console.log(canvasId, chartData)
				canvaColumnWeek = new uCharts({
					$this: self,
					canvasId: canvasId,
					type: chartData.type,
					colors: ["#8dc63f"],
					legend: { show: true },
					fontSize: 11,
					background: '#FFFFFF',
					pixelRatio: self.pixelRatio,
					animation: true,
					categories: chartData.categories,
					series: chartData.series,
					xAxis: {
						disableGrid: true,
					},
					yAxis: {
						//disabled:true
					},
					dataLabel: true,
					width: self.cWidth * self.pixelRatio,
					height: self.cHeight * self.pixelRatio,
					extra: {
						column: {
							type: 'group',
							width: self.cWidth * self.pixelRatio * 0.45 / chartData.categories.length
						}
					}
				});
			}

			// console.log(canvaColumn)
		},

		touchColumn(e) {

			canvaColumn.showToolTip(e, {
				format: function(item, category) {
					if (typeof item.data === 'object') {
						return category + ' ' + item.name + ':' + item.data.value
					} else {
						return category + ' ' + item.name + ':' + item.data
					}
				}
			});
		},

		touchColumnWeek(e) {

			canvaColumnWeek.showToolTip(e, {
				format: function(item, category) {
					if (typeof item.data === 'object') {
						return category + ' ' + item.name + ':' + item.data.value
					} else {
						return category + ' ' + item.name + ':' + item.data
					}
				}
			});
		},

		getStep() {
			let code;
			try {
				uni.getProvider({
					service: 'oauth',
					success: function(res) {
						console.log(res.provider)
						if (~res.provider.indexOf('weixin')) {
							uni.login({
								success(res) {
									if (res.code) {
										code = res.code;

										wx.getWeRunData({
											async success(res) {
												res.code = code;
												const step = await getStep(res);
												if (step.status === 0) {
													step.data.stepInfoList.map((item, index) => {
														let date = new Date()

														date.setTime(item.timestamp * 1000);
														console.log(date.getDate())
														if (index % 5 === 0) {
															self.chartData.categories.push(date.getMonth() + 1 + "-" + date.getDate())
															self.chartData.series[0].data.push(item.step)
														}
														if (index > 23) {
															self.chartDataWeek.categories.push(date.getMonth() + 1 + "-" + date.getDate())
															console.log(date.getDate(), item.step)
															self.chartDataWeek.series[0].data.push(item.step)
														}
														// self.chartData.categories[Math.floor(index / 5)].push(date.getMonth() + 1 + "-" + date.getDate())
													})
													console.log(self.chartData.categories, self.chartData.series[0].data)
													self.showColumn("canvasColumn", self.chartData, "")
													self.showColumn("canvasColumn2", self.chartDataWeek, 1)
												} else {
													throw new Error("解密失败")
												}
											}
										})

									} else {
										console.log('登录失败！' + res.errMsg)
									}
								}
							})
						}
					}
				});
			} catch (e) {
				console.log(e)
				uni.showToast({
					title: "系统异常",
					icon: "none"
				})
				//TODO handle the exception
			}
		}

	},

	created() {
		this.getStep()
	},

	onShow() {
		self = this











		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		// this.getServerData();
		// this.init()
	}
}
